//入口文件
import FastClick from 'fastclick';
import Vue from 'vue';
import VueRouter from 'vue-router';
import touch from './utils/vue-touch';
import VueI18n from 'vue-i18n';
import router from './router/';
import Locales from './locale';
import { setLoc, getLoc } from './utils';
import MintUI from 'mint-ui';
import components from './components/index.js'; // 加载公共组件
import AlloyFinger from 'alloyfinger';
import AlloyFingerPlugin from 'alloyfinger/vue/alloy_finger.vue';
import store from './store';
import App from './App'; // 主入口
import vConsole from 'vconsole';
// const vConsole2 = new vConsole();

//全局引入第三方插件
Vue.use(MintUI);
Vue.use(VueRouter);
Vue.use(AlloyFingerPlugin, {
  AlloyFinger
});
Vue.use(VueI18n);
Vue.directive('touch', touch);

if ('addEventListener' in document) {
  FastClick.attach(document.body);
}

let locale = window.Android.getLanguage() ? 'zh' : 'en';
store.commit('setLang', locale);

const messages = Object.create(null);
Object.keys(Locales).forEach(local => {
  messages[local] = {
    ...Locales[local]
  };
});
const i18n = new VueI18n({
  locale,
  messages
});

Vue.config.productionTip = false;

Object.keys(components).forEach(key => {
  var name = key.replace(/(\w)/, v => v.toUpperCase()); //首字母大写
  Vue.component(`vx${name}`, components[key]);
});

//横竖屏切换
store.commit('setIsHScreen', window.orientation == 90 || window.orientation == -90);
window.addEventListener('onorientationchange' in window ? 'orientationchange' : 'resize', function() {
  store.commit('setIsHScreen', window.orientation == 90 || window.orientation == -90);
});

new Vue({
  router,
  i18n,
  store,
  render: h => h(App)
}).$mount('#app');



