<template>
  <div>
    <div class="message-box">
      <div class="title">{{title}}</div>
      <div class="message">
        <span>{{message}}</span>
      </div>
      <div class='commit'>
        <div class="btn" @click='sure'>{{$t('scenarist.ok')}}</div>
      </div>
    </div>
    <div class="mask-model"></div>
  </div>
</template>

<script>
  export default {
    props: ['message'],
    data() {
      return {
        title: this.$t('common.tip')
      };
    },
    methods: {
      sure() {
        this.$emit('sureText');
      }
    }
  };
</script>
<style lang="less" scoped>
  .message-box {
    width: 531px;
    height: 292px;
    background: rgba(106, 110, 124, 0.96);
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    border-radius: 16px;
    padding-top: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    color: #fff;
  }

  .title {
    font-size: 34px;
  }

  .message {
    width: 541px;
    height: 59px;
    border-radius: 16px;
    font-size: 30px;
    > span {
      padding: 0 20px;
    }
  }

  .message > input {
    width: 100%;
    height: 100%;
    display: block;
    outline: none;
    border: 1px solid #ccc;
    /*no*/
    border-radius: 16px;
  }

  .commit {
    width: 100%;
    height: 94px;
    border-radius: 0 0 16px 16px;
    display: flex;
    justify-content: space-between;
    border-top: 1px solid #8890a2;
    font-size: 30px;
    div {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    div:nth-child(1) {
      flex: 1;
      height: 100%;
      background: #565a65;
      border-radius: 0 0 0 16px;

      border-radius: 0 0 16px 16px;
    }
    div:nth-child(2) {
      flex: 1;
      height: 100%;
      background: #eef0f1;
      border-radius: 0 0 16px 0;
      border-left: 1px solid #8890a2;
      color: #f56800;
    }
  }
  .mask-model {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    z-index: 9;
  }
</style>


