<template>
  <div>
    <div class="indicator">
      <div class="ani"></div>
      <div class="text">{{process}}%</div>
      <span class="note">{{$t('osd.leftTime')}} {{leftTime}}</span>
      <div class="cancel">
        <div class="X" @click="$emit('progressCancel')">X</div>
        <div class="shu"></div>
      </div>
    </div>
    <div class="indicator-mask"></div>
  </div>
</template>

<script>
  // import htp from "../../api/http";
  // import urls from '../../api/apiurl';
  export default {
    props: ['text', 'time'],
    beforeCreate() {
      window.showImageLoading = true;
    },
    destroyed() {
      window.showImageLoading = false;
    },
    data() {
      return {
        visible: false,
        process: 0
      };
    },
    computed: {
      leftTime() {
        let time = this.time;
        this.process = this.text;

        if(this.process < 0) {
          this.process = 0;
        }
        if(this.process === 100) {
          time = 0;
        }
        if(this.process < 100 && time === 0) {
          time = 1;
        }

        let hours = Math.floor(time / 3600);
        let minutes = Math.floor(time % 3600 / 60);
        let seconds = time % 60;

        let result = '';
        if(hours) {
          result = hours + this.$t('osd.hours');
        }
        if(minutes) {
          result += minutes + this.$t('osd.minutes');
        }
        result += seconds + this.$t('osd.seconds');

        return result;
      }
    },
    methods: {
      cancel() {
        htp.post(urls.getProgress, { control: 0 }).then(res => {
          console.log(res);
          if(res.status === 10000000) {
            this.visible = false;
          }
        });
      }
    }
  };
</script>
<style scoped lang="less">
  .indicator {
    background: rgba(106, 110, 124, 0.96);
    width: 400px;
    z-index: 10;
    padding: 20px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 23px;
    color: #ffffff;
    .text {
      font-size: 40px;
      width: 100%;
    }
    .note {
      font-size: 20px;
    }
    .cancel {
      position: absolute;
      height: 120px;
      width: 66px;
      top: -120px;
      right: 0;
      .X {
        width: 66px;
        height: 66px;
        border-radius: 50%;
        border: 1px solid #ddd; /*no*/
        color: #ddd;
        font-size: 40px;
        text-align: center;
        line-height: 66px;
      }
      .shu {
        width: 1px; /*no*/
        height: 58px;
        margin: auto;
        background: #ffffff;
      }
    }
  }
  .indicator-mask {
    width: 100%;
    height: 100%;
    position: fixed;
    background: rgba(0, 0, 0, 0.6);
    z-index: 5;
    top: 0;
  }
  .ani {
    border: 3px solid; /*no*/
    border-top-color: #737886;
    border-left-color: #737886;
    border-bottom-color: #737886;
    border-right-color: #ffffff;
    height: 88px;
    width: 88px;
    margin: 30px auto;
    border-radius: 50%;
    animation: rotate 2s linear infinite;
  }
  @keyframes rotate {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style>


