const router = [
  /**在线模式 */
  {
    name: 'index', //首页
    path: '/',
    component: () => import('../pages/index')
  },
  {
    name: 'selectScreenMode', //配屏
    path: '/filamentShield',
    component: () => import('../pages/selectScreenMode')
  },
  {
    name: 'quickscreen', //快捷点屏
    path: '/filamentShield/quickscreen',
    component: () => import('../pages/selectScreenMode/quickscreen-h')
  },
  {
    name: 'advancedScreen', //高级点屏
    path: '/filamentShield/advancedScreen/advancedScreen',
    component: () => import('../pages/selectScreenMode/advancedScreen')
  },
  {
    name: 'networkPort_ModalBox', //选择网口
    path: '/filamentShield/networkPort_ModalBox',
    component: () => import('../pages/selectScreenMode/networkPort_ModalBox')
  },
  {
    name: 'scenarist', //编导
    path: '/scenarist',
    component: () => import('../pages/scenarist/scenarist')
  },
  {
    name: 'scenarist-h', //编导
    path: '/scenarist-h',
    component: () => import('../pages/scenarist/scenarist-h')
  },
  {
    name: 'selectmode', //选择模式
    path: '/scenarist/selectmode',
    component: () => import('../pages/selmode')
  },
  {
    name: 'cut', //源截取
    path: '/scenarist/selectmode/cut',
    component: () => import('../pages/scenarist/cut')
  },
  {
    name: 'scenaristLayout', //窗口布局
    path: '/scenarist/selectmode/scenaristLayout',
    component: () => import('../pages/scenarist/scenaristLayout')
  },
  {
    name: 'quality', //高级设置
    path: '/scenarist/selectmode/quality',
    component: () => import('../pages/scenarist/quality')
  },
  {
    name: 'window-border', //边框
    path: '/scenarist/selectmode/window-border',
    component: () => import('../pages/scenarist/window-border')
  },
  {
    name: 'savesc', //保存编导
    path: '/scenarist/selectmode/savesc',
    component: () => import('../pages/scenarist/saveScena')
  },
  {
    name: 'window', //源切换
    path: '/window',
    component: () => import('../pages/window')
  },
  {
    name: 'scene', //场景
    path: '/scene',
    component: () => import('../pages/scene')
  },
  {
    name: 'psytopic', //测试画面
    path: '/psytopic',
    component: () => import('../pages/test')
  },
  {
    name: 'settings', //设置
    path: '/settings',
    component: () => import('../pages/settings/settings')
  },
  {
    name: 'enterconfig', //输入配置
    path: '/settings/enterconfig',
    component: () => import('../pages/settings/enterConfig')
  },
  {
    name: 'selectsource', //选择源
    path: '/settings/enterconfig/selectsource',
    component: () => import('../pages/settings/selSources')
  },
  {
    name: 'selresolution', //选择分辨率
    path: '/settings/enterconfig/selresolution',
    component: () => import('../pages/settings/selResolution')
  },
  {
    name: 'led', //led画质调整
    path: '/settings/led',
    component: () => import('../pages/settings/led')
  },
  {
    name: 'selmode', //选择模式
    path: '/settings/selmode',
    component: () => import('../pages/settings/selmode')
  },
  {
    name: 'takeeffects', //take特效
    path: '/settings/takeeffects',
    component: () => import('../pages/settings/takeEffects')
  },
  {
    name: 'sceneeffects', //场景特效
    path: '/settings/sceneeffects',
    component: () => import('../pages/settings/sceneEffects')
  },
  {
    name: 'help', //帮助
    path: '/settings/help',
    component: () => import('../pages/settings/help')
  },
  {
    name: 'versions', //硬件版本信息
    path: '/settings/help/versions',
    component: () => import('../pages/settings/versions')
  },
  {
    name: 'languages', //语言
    path: '/settings/languages',
    component: () => import('../pages/settings/languages')
  },
  {
    name: 'update', //更新
    path: '/settings/update',
    component: () => import('../pages/settings/update')
  },
  {
    name: 'verdetail', //关于本次更新
    path: '/settings/update/verdetail',
    component: () => import('../pages/settings/verdetail')
  },
  {
    name: 'password', //修改密码
    path: '/settings/password',
    component: () => import('../pages/settings/password')
  },
  {
    name: 'osd', //osd
    path: '/osd',
    component: () => import('../pages/osd/osd')
  },
  {
    name: 'editosd', //editosd
    path: '/osd/editosd',
    component: () => import('../pages/osd/edit')
  },
  {
    name: 'osdtext',
    path: '/osd/osdtext',
    component: () => import('../pages/osd/osdtext')
  },
  {
    name: 'about', //osd
    path: '/about',
    component: () => import('../pages/about')
  },
  {
    path: '/', //其他页面，强制跳转到登录页面
    redirect: '/'
  }
];

export default router;



