<template>
  <div class="vx-header">
    <mt-header>
      <!-- Home键 -->
      <mt-button slot="left" class="left-btn" v-if="showhome" @click="$emit('home')">
        <img src="../assets/common/back.png" slot="icon">
      </mt-button>
      <!-- 返回上一路由 -->
      <mt-button slot="left" class="left-btn" v-if="showback" @click="back">
        <img src="../assets/common/back.png" slot="icon">
      </mt-button>
      <!-- 取消  -->
      <mt-button slot="left" class="left-text" v-if="showcancel" @click="$emit('cancel')">{{$t('common.cancel')}}</mt-button>
      <!-- 上一步 -->
      <mt-button slot="left" class="right-text" v-if="showprev" @click="back">{{$t('common.prevStep')}}</mt-button>
      <!-- 确定 -->
      <mt-button slot="right" class="right-text" v-if="showsave" @click="$emit('save')">{{$t('common.confirm')}}</mt-button>
      <!-- 重置右 -->
      <mt-button slot="right" class="right-btn" v-if="showreset" @click="$emit('reset')">
        <img src="../assets/common/refresh.png" slot="icon">
      </mt-button>
      <!-- 刷新右-->
      <mt-button slot="right" class="right-btn" v-if="showrrefresh" @click="refresh">
        <img src="../assets/common/refresh.png" slot="icon">
      </mt-button>
      <!-- 保存 -->
      <mt-button slot="right" class="right-btn" v-if="showsubmit" @click="$emit('submit')">
        <img src="../assets/common/save.png" slot="icon">
      </mt-button>
      <!-- 保存 无图版-->
      <mt-button slot="right" class="right-text" v-if="showOsdFont" @click="$emit('savesFont')">
        <span class="complete">{{$t('common.save')}}</span>
      </mt-button>
      <!-- 下一步 -->
      <mt-button slot="right" class="right-text" v-if="shownext" @click="$emit('next')">{{$t('common.nextStep')}}</mt-button>
      <!-- 完成 -->
      <mt-button slot="right" class="right-text complete" :class="{disabled}" v-if="showcomplete" :disabled="disabled" @click="$emit('complete')">{{$t('common.complete')}}</mt-button>
      <!-- 恢复默认 -->
      <mt-button slot="right" class="right-text" v-if="showdefault" @click="$emit('default')">{{$t('common.default')}}</mt-button>
    </mt-header>
    <div class="vx-header_title" v-html="title"></div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
      };
    },
    props: [
      'showhome',       //控制Home键是否显示
      'showback',       //控制返回上一路由键（<）是否显示
      'showrrefresh',   //控制刷新右按钮是否显示
      'showcancel',     //控制取消按钮是否显示
      'showsave',       //控制确定按钮是否显示
      'showsubmit',     //控制保存按钮是否显示
      'shownext',       //控制下一步按钮是否显示
      'showreset',      //控制重置右按钮是否显示
      'showcomplete',   //控制完成按钮是否显示
      'showdefault',    //控制恢复默认按钮是否显示
      'title',          //标题
      'disabled',       //是否禁用按钮
      'ishome',         //是否首页
      'showOsdFont',     //保存，无图片版本
      'showprev',
      'lazy'
    ],
    methods: {
      back() {
        this.$emit('back');
        if(this.ishome) {
          window.Android.returnDeviceList();
        } else {
          if(this.lazy) {
          } else {
            this.$router.go(-1);
          }
        }
      },
      refresh() {
        this.$emit('refresh');
      }
    }
  };
</script>

<style scoped lang="less">
  .vx-header {
    position: absolute;
    width: 100%;
    z-index: 2;
    left: 0;
    top: 0;
    .mint-header {
      background-color: #2d2d36;
      height: 80px;
      font-size: 16px; /*no*/
    }
    &_title {
      position: absolute;
      top: 0;
      left: 20%;
      width: 60%;
      height: 80px;
      line-height: 80px;
      text-align: center;
      background: #2d2d36;
      font-size: 19px; /*no*/
      color: #ddd;
      z-index: 2;
    }
    img {
      width: 36px;
      height: 36px;
    }
    .left-text,
    .right-text {
      font-size: 28px;
      color: #ddd;
      padding: 0 28px;
      height: 80px;
      line-height: 80px;
    }
    .mint-button-text {
      font-size: 28px;
    }
    .complete {
      color: #f56800;
      &.disabled {
        color: rgba(245, 104, 0, 0.5);
      }
    }
  }
</style>


