<template>
  <div class="vc-cell btn" @click="linkTo" :class="{'tap-active': showLink}">
    <div class="vc-cell-wrapper">
      <div class="vc-cell-title">
        <slot name="icon">
          <span v-if="icon" class="vc-cell-icon" :class="icon"></span>
        </slot>
        <slot name="title">
          <span class="vc-cell-text" v-text="title"></span>
        </slot>
      </div>
      <div class="vc-cell-value" v-if="value||showBadge" :class="{link: showLink}">
        <slot>
          <mt-badge class="badge" v-if="showBadge" type="error">1</mt-badge>
          <span v-text="value" v-else></span>
        </slot>
      </div>
      <i v-if="showLink" class="mint-cell-allow-right"></i>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      title: String,
      icon: String,
      value: '',
      showLink: {
        type: Boolean,
        default: true
      },
      showBadge: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      linkTo() {
        this.$emit('link');
      }
    }
  };
</script>

<style lang="less" scoped>
  .tap-active:active {
    background-color: rgba(0, 0, 0, 0.4);
  }
  .badge {
    display: inline-block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    padding: 0 !important;
    border-radius: 50%;
  }
  .vc-cell {
    background-color: #383940;
    box-sizing: content-box;
    color: inherit;
    display: block;
    overflow: hidden;
    position: relative;
    text-decoration: none;
    &-wrapper {
      align-items: center;
      box-sizing: content-box;
      display: flex;
      font-size: 32px;
      padding: 25px;
      overflow: hidden;
    }
    &-title {
      flex: 1;
      text-align: left;
      color: #dddddd;
      display: flex;
      align-items: center;
      .vc-cell-icon {
        margin-right: 20px;
        width: 51px;
        height: 51px;
        background-size: 51px 51px;
        background-position: center center;
      }
      .vc-cell-text {
        line-height: 51px;
      }
    }
    &-value {
      width: 50%;
      display: flex;
      align-items: center;
      color: #aeaeae;
      font-size: 15px; /*no*/
      text-align: right;
      justify-content: flex-end;
      &.link {
        padding-right: 35px;
      }
    }
    .mint-cell-allow-right::after {
      width: 16px;
      height: 16px;
      right: 35px;
    }
  }
  //settings
  .without {
    background: url('../assets/effects/without.png') no-repeat #383940;
  }

  .cut {
    background: url('../assets/effects/cut.png') no-repeat #383940;
  }

  .fade {
    background: url('../assets/effects/fade.png') no-repeat #383940;
  }

  .wipe-top-left {
    background: url('../assets/effects/wipe-top-left.png') no-repeat #383940;
  }

  .wipe-left {
    background: url('../assets/effects/wipe-left.png') no-repeat #383940;
  }

  .wipe-bottom-left {
    background: url('../assets/effects/wipe-bottom-left.png') no-repeat #383940;
  }

  .wipe-bottom {
    background: url('../assets/effects/wipe-bottom.png') no-repeat #383940;
  }

  .wipe-bottom-right {
    background: url('../assets/effects/wipe-bottom-right.png') no-repeat #383940;
  }

  .wipe-right {
    background: url('../assets/effects/wipe-right.png') no-repeat #383940;
  }

  .wipe-top-right {
    background: url('../assets/effects/wipe-top-right.png') no-repeat #383940;
  }

  .wipe-top {
    background: url('../assets/effects/wipe-top.png') no-repeat #383940;
  }

  .split-horizontally-out {
    background: url('../assets/effects/split-horizontally-out.png') no-repeat #383940;
  }

  .split-vertically-out {
    background: url('../assets/effects/split-vertically-out.png') no-repeat #383940;
  }

  .zoom-in {
    background: url('../assets/effects/zoom-in.png') no-repeat #383940;
  }

  .cover-top-left {
    background: url('../assets/effects/cover-top-left.png') no-repeat #383940;
  }

  .cover-left {
    background: url('../assets/effects/cover-left.png') no-repeat #383940;
  }

  .cover-bottom-left {
    background: url('../assets/effects/cover-bottom-left.png') no-repeat #383940;
  }

  .cover-bottom {
    background: url('../assets/effects/cover-bottom.png') no-repeat #383940;
  }

  .cover-bottom-right {
    background: url('../assets/effects/cover-bottom-right.png') no-repeat #383940;
  }

  .cover-top {
    background: url('../assets/effects/cover-top.png') no-repeat #383940;
  }

  .split-horizontally-in {
    background: url('../assets/effects/split-horizontally-in.png') no-repeat #383940;
  }

  .split-vertically-in {
    background: url('../assets/effects/split-vertically-in.png') no-repeat #383940;
  }

  .zoom-out {
    background: url('../assets/effects/zoom-out.png') no-repeat #383940;
  }

  .cover-right {
    background: url('../assets/effects/cover-right.png') no-repeat #383940;
  }

  .cover-top-right {
    background: url('../assets/effects/cover-top-right.png') no-repeat #383940;
  }

  .zoom-out-rectangle {
    background: url('../assets/effects/zoom-out-rectangle.png') no-repeat #383940;
  }

  .zoom-in-rectangle {
    background: url('../assets/effects/zoom-in-rectangle.png') no-repeat #383940;
  }

  .zoom-in-diamond {
    background: url('../assets/effects/zoom-in-diamond.png') no-repeat #383940;
  }

  .zoom-out-diamond {
    background: url('../assets/effects/zoom-out-diamond.png') no-repeat #383940;
  }

  .zoom-in-circular {
    background: url('../assets/effects/zoom-in-circular.png') no-repeat #383940;
  }

  .zoom-out-circular {
    background: url('../assets/effects/zoom-out-circular.png') no-repeat #383940;
  }
  .nosignal {
    background-color: #c2c2c2;
    width: 18px !important;
    height: 18px !important;
    border-radius: 4px;
  }
  .signal {
    background-color: #0ceb46;
    width: 18px !important;
    height: 18px !important;
    border-radius: 4px;
  }
</style>




