<template>
  <div class="vx-progress">
    <div class="title-box">
      <span class="title">{{title}}</span>
      <div class="plusminus">
        <mt-button class="minus" @touchstart.native="minusStart" @touchend.native="minusEnd" type="default" size="small">
          <img src="../assets/common/minus.png" />
        </mt-button>
        <mt-button class="plus" @touchstart.native="plusStart" @touchend.native="plusEnd" type="default" size="small">
          <img src="../assets/common/plus.png" />
        </mt-button>
      </div>
      <span class="number" v-if="custom">{{$t('settings.custom')}}</span>
      <span class="number" v-show="irregular">{{nums/100}}</span>
      <input class="number input" type="number" :value="nums" @blur="blur(nums,$event)" v-if="isinput">
      <span class="number" v-show="!irregular&&!custom&&!isinput">{{nums}}{{prefix}}</span>
    </div>
    <div class="progress-box" @touchend="$emit('touchend')">
      <mt-range v-model="nums" :min="range.start" :max="range.end" :step="step" :bar-height="2">
      </mt-range>
    </div>
  </div>
</template>

<script>
  import { validateNum } from '../utils';
  import { Toast } from 'mint-ui';
  export default {
    /**
     * @param title 标题
     * @param num 当前大小
     * @param range 取值范围
     * @text <vx-progress :title="'全部'" :num="5" :range="range"></vx-progress>
     */
    props: {
      prefix: {
        type: String,
        default: ''
      },
      title: {
        type: String,
        default: ''
      },
      num: {
        type: Number,
        default: 0
      },
      step: {
        type: Number,
        default: 1
      },
      range: {
        type: Object,
        default: {}
      },
      index: {
        type: Number,
        default: 0
      },
      irregular: {
        type: Boolean,
        default: false
      },
      custom: {
        type: Boolean,
        default: false
      },
      isinput: {
        type: Boolean,
        default: false
      }
    },
    data: function() {
      return {
        nums: 0,
        interval: null,
        count: 0

      };
    },
    created() {
      this.iscreated = false;
      this.nums = this.num;
    },
    methods: {
      plusHandler() {
        if(this.nums < this.range.end) {
          this.nums = this.nums + this.step;
        }
        if(this.nums === this.range.end) {
          this.nums = this.range.end;
        }
      },
      minusHandler() {
        if(this.nums > this.range.start) {
          this.nums = this.nums - this.step;
        }
      },
      emitHandler() {
        setTimeout(() => {
          this.$emit('touchend');
        }, 0);
      },
      plusStart() {
        this.interval = setInterval(() => {
          this.plusHandler();
        }, 100);
      },
      plusEnd() {
        clearInterval(this.interval);
        this.plusHandler();
        this.emitHandler();
      },
      minusStart() {
        this.interval = setInterval(() => {
          this.minusHandler();
        }, 100);
      },
      minusEnd() {
        clearInterval(this.interval);
        this.minusHandler();
        this.emitHandler();
      },
      blur(nums, $event) {
        if(validateNum($event.target.value)) {
          if(parseInt($event.target.value) > this.range.end) {
            Toast(this.$t('common.inputError'));
            this.nums = this.range.end;
          } else {
            this.nums = parseInt($event.target.value);
          }
        } else {
          Toast(this.$t('common.number'));
          this.nums = 0;
        }
        this.emitHandler();
      }
    },
    watch: {
      nums: function(newVal) {
        if(this.count) {
          this.$emit('update', newVal, this.index);
        }
        this.count++;
      },
      num: function(newVal) {
        this.nums = newVal;
      }
    }
  };
</script>

<style lang="less" scoped>
  /*progress*/

  .mt-range {
    width: 100%;
    height: 50px;
    line-height: 50px;
    .mt-range-thumb {
      width: 50px;
      height: 50px;
    }
  }

  .vx-progress {
    color: #ddd;
    border-bottom: 1px #4f5054 solid;
    /*no*/
    height: 160px;
    .title-box {
      height: 50px;
      position: relative;
      .title {
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        height: inherit;
      }
      .number {
        display: block;
        position: absolute;
        top: 0;
        right: 250px;
      }
      .input {
        width: 120px;
        background: #383940;
        border: 0;
      }
      .title,
      .number {
        color: #ddd;
        height: 50px;
        line-height: 50px;
        font-size: 30px;
      }
      .plusminus {
        position: absolute;
        top: 0;
        height: inherit;
        display: flex;
        width: 190px;
        right: 35px;
        border-radius: 8px;
        justify-content: space-between;
        .plus,
        .minus {
          display: block;
          flex: 1;
          height: 100%;
          background: none;
          border-radius: 0;
          img {
            width: 28px;
            height: 28px;
          }
        }
      }
    }
    .progress-box {
      padding: 10px 35px;
    }
  }
</style>


