<template>
  <transition name="vxactionsheet">
    <div v-show="visible" class="vx-actionsheet">
      <div style="z-index:100">
        <ul class="vx-actionsheet-list">
          <li v-for="( item, index ) in actions" class="vx-actionsheet-listitem" @click.stop="clickHandler(item, index)">
            <span class="icon" v-show="item.icon" :class="item.icon"></span>{{ item.name }}
          </li>
        </ul>
        <a class="vx-actionsheet-button" @click.stop="cancel" v-if="cancelText">{{ cancelText }}</a>
      </div>
    </div>
  </transition>
</template>

<script>
  export default {
    props: {
      visible: {
        default: false,
        type: Boolean
      },
      actions: {
        default: [],
        type: Array
      },
      cancelText: {
        type: String,
        default: ''
      }
    },
    methods: {
      clickHandler(item, index) {
        if(item.method && typeof item.method === 'function') {
          item.method(item, index);
        }
      },
      cancel() {
        this.$emit('cancel');
      }
    }
  };
</script>

<style lang="less" scoped>
  .vx-actionsheet {
    position: fixed;
    width: 100%;
    text-align: center;
    bottom: 0;
    left: 50%;
    max-height: 100%;
    overflow-y: auto;
    z-index: 100;
    transform: translate3d(-50%, 0, 0);
    backface-visibility: hidden;
    transition: transform 0.3s ease-out;
  }
  .vx-actionsheet {
    &-list {
      list-style: none;
      padding: 0;
      margin: 20px;
      margin-top: 0;
      border-radius: 20px;
      overflow: hidden;
    }
    &-button {
      margin: 20px;
      border-radius: 20px;
    }
    &-listitem {
      border-bottom: solid 1px #787e8d; /*no*/
      .icon {
        display: inline-block;
        width: 40px;
        height: 34px;
        background-size: 40px 34px;
        background-position: center;
        margin-right: 20px;
      }
    }
    &-listitem,
    &-button {
      display: block;
      width: auto;
      height: 100px;
      line-height: 100px;
      font-size: 36px;
      color: #fff;
      background: #6b707d;
      opacity: 0.94;
      &:active {
        background-color: rgba(0, 0, 0, 0.4);
      }
    }
  }

  .vxactionsheet-enter,
  .vxactionsheet-leave-active {
    transform: translate3d(-50%, 100%, 0);
  }
</style>



