<template>
  <!-- <div class="tcnbtn-layout" v-finger:press-move="pressMove" @click="startTap" ref="btn" v-touch.swipe="text"> -->
  <!-- <div class="tcnbtn-layout" v-finger:press-move="pressMove" @tap="startTap" ref="btn"> -->
  <div>

    <div class="tcnbtn-layout" :class="{ changeLayoutBottomCenter:changeLayoutBottomCenter ,changeLayoutTopCenter:changeLayoutTopCenter ,changeLayoutCenter:changeLayoutCenter, changeLayoutRightCenter:changeLayoutRightCenter,changeLayoutLeftCenter:changeLayoutLeftCenter, changeLayoutLeftBottom:changeLayoutLeftBottom, changeLayoutLeftTop:changeLayoutLeftTop, changeLayoutRightTop:changeLayoutRightTop,changeLayoutRightBottom:changeLayoutRightBottom}" v-touch.pan.tap="pressMove" ref="btn" data-index="0">

      <div class="btnBox" v-show="modelBox">
        <div class="take btn" @click="take">
          <img v-if="TCSflag" src="../assets/take/take.png" alt="">
          <img v-else src="../assets/take/takefalse.png" alt="">
          <span :class="{color: !TCSflag}" v-show="timeOut">TAKE</span>
        </div>
        <div class="cuts btn" @click="cut">
          <img v-if="TCSflag" src="../assets/take/cut.png" alt="">
          <img v-else src="../assets/take/cutfalse.png" alt="">
          <span :class="{color: !TCSflag}" v-show="timeOut">CUT</span>
        </div>
        <div class="swap btn" @click="swap">
          <img v-if="TCSflag" src="../assets/take/swap.png" alt="">
          <img v-else src="../assets/take/swapfalse.png" alt="">
          <span :class="{color:!TCSflag}" v-show="timeOut">SWAP</span>
        </div>
        <div class="freeze" @click.stop="freeze" :class="{border: icoScreen}">
          <img v-if="icoScreen" src="../assets/take/iceben.png" alt="">
          <img v-else-if="!isblcIcoScreen && !TCSflag" src="../assets/take/noiceBen.png" alt="">
          <img v-else src="../assets/take/ice.png" alt="">
          <span :class="{color:!TCSflag}" v-show="timeOut">{{$t('common.icoScreenName')}}</span>
        </div>
        <div class="black-screen" @click.stop="blackScreen" :class="{border: blcScreen}">
          <img v-if="blcScreen" src="../assets/take/blackScreenben.png" alt="">
          <img v-else-if="!isblcIcoScreen && !TCSflag" src="../assets/take/noblackScreen.png" alt="">
          <img v-else src="../assets/take/blackScreen.png" alt="">
          <span :class="{color:!TCSflag}" v-show="timeOut">{{$t('common.blackScreenName')}}</span>
        </div>
        <div class="save-screen" @click="saveScreen" v-show="showSaveScreen">
          <img src="../assets/take/saveSelect.png" alt="">
          <span :class="{color:!TCSflag}" v-show="timeOut">{{$t('scenes.saveScene')}}</span>
        </div>
      </div>
      <div class="active" v-show="!modelBox">
        <div></div>
        <div></div>
        <div></div>
      </div>
    </div>
    <div class="model-box" v-show="modelBox" @click="reduction">
    </div>
  </div>
</template>
<script>
  import htp from '../api/http';
  import url from '../api/apiurl';
  import { setLoc, getLoc, setSen, getSen, clearLocalKey } from '../utils';
  import { Toast } from 'mint-ui';
  export default {
    created() {
      if(this.$router.history.current.name === 'window' || this.$router.history.current.name === 'scene') {
        this.showSaveScreen = false;
      }
      if(getLoc('isTextTap') === '0') {
        this.blcScreen = true;
      } else if(getLoc('isTextTap') === '2') {
        this.icoScreen = true;
        // this.TCSflag = false;
      }

      // this.getTestPicture();
    },
    data() {
      return {
        timeOut: false,
        changeLayout: [
          'changeLayoutLeftBottom',
          'changeLayoutLeftTop',
          'changeLayoutRightTop',
          'changeLayoutRightBottom',
          'changeLayoutLeftCenter',
          'changeLayoutRightCenter',
          'changeLayoutCenter',
          'changeLayoutTopCenter',
          'changeLayoutBottomCenter'
        ],  //判断位置
        changeLayoutLeftBottom: false, //按钮在屏幕的左下
        changeLayoutLeftTop: false, //---左上
        changeLayoutRightTop: false, //右上
        changeLayoutRightBottom: false, //右下
        changeLayoutLeftCenter: false,
        changeLayoutRightCenter: false,
        changeLayoutCenter: false,
        changeLayoutTopCenter: false,
        changeLayoutBottomCenter: false,
        chageName: '',
        modelBox: false,
        tapEnd: true,
        showSaveScreen: true,
        TCSflag: true,
        icoScreen: false,
        blcScreen: false, //黑屏
        icoBlaScreen: true, //冻结
        isblcIcoScreen: true //黑屏冻结均不可操作 true可操作 false不可操作
      };
    },
    props: [],
    methods: {
      getTestPicture() {
        htp.get(url.getTestPicture).then(res => {
          if(res.status === 10000000) {
            console.log(res);
            switch(res.data.testControl) {
              case (0):
                console.log('冻结黑屏正常使用');
                this.isblcIcoScreen = true;
                this.TCSflag = true;
                this.icoScreen = false;
                this.blcScreen = false;
                // setLoc('isTextTap', 1);
                break;
              case (1):
                console.log('冻结黑屏无法使用,take...无法使用');
                this.isblcIcoScreen = false;
                this.TCSflag = false;
                Toast(this.$t('common.isib'));
                break;
              case (2):
                console.log('硬件画面冻结');
                this.icoScreen = true;
                this.blcScreen = false;
                // setLoc('isTextTap', 2);
                break;
              case (3):
                console.log('硬件画面黑屏');
                this.blcScreen = true;
                this.icoScreen = false;
                // setLoc('isTextTap', 0);
                break;
              default:
                // this.isblcIcoScreen = true;
                break;
            }
          }
        });
      },
      reduction() {
        this[this.chageName] = false;
        this.modelBox = false;
        this.tapEnd = true;
        this.timeOut = false;
      },
      text() {
        this[this.chageName] = false;
        this.modelBox = false;
        let that = this;
        setTimeout(function() {
          that.tapEnd = true;
        }, 1000);
      },
      take() {
        if(this.TCSflag) {
          htp.post(url.commandMessage, { takeCommand: 0 });
          this[this.chageName] = false;
          this.modelBox = false;
          this.timeOut = false;
          let that = this;
          setTimeout(function() {
            that.tapEnd = true;
          }, 1000);
        }
      },
      cut() {
        if(this.TCSflag) {
          htp.post(url.commandMessage, { takeCommand: 2 });
          this[this.chageName] = false;
          this.modelBox = false;
          this.timeOut = false;
          let that = this;
          setTimeout(function() {
            that.tapEnd = true;
          }, 1000);
        }

      },
      swap() {
        if(this.TCSflag) {
          htp.post(url.commandMessage, { takeCommand: 1 }).then(res => {
            if(res.status === 10000000) {
              if(this.$parent.reload) {
                this.$parent.reload();
              }
            }
          });
          this[this.chageName] = false;
          this.modelBox = false;
          this.timeOut = false;
          let that = this;
          setTimeout(function() {
            that.tapEnd = true;
          }, 1000);
        }

      },
      freeze() {
        if(this.isblcIcoScreen) {
          this.icoScreen = !this.icoScreen;
          if(this.icoScreen) {
            htp.post(url.setTestPicture, { testControl: 2, testPattern: 0 });
            console.log('冻结');
            this.blcScreen = false;
          } else {
            htp.post(url.setTestPicture, { testControl: 0, testPattern: 0 });
            console.log('取消冻结');
          }
        } else {
          console.log('当前为测试画面冻结不能使用');
          Toast(this.$t('common.isicoScreen'));
        }

      },
      blackScreen() {
        if(this.isblcIcoScreen) {
          if(!this.blcScreen) {
            console.log('黑屏');
            htp.post(url.setTestPicture, { testControl: 3, testPattern: 0 });
            this.blcScreen = true;
            this.icoScreen = false;
            this.TCSflag = true;
            setLoc('isTextTap', 0);
          } else {
            console.log('取消黑屏');
            htp.post(url.setTestPicture, { testControl: 0, testPattern: 0 });
            this.blcScreen = false;
            clearLocalKey('isTextTap');
          }
        } else {
          console.log('当前为测试画面黑屏不能使用');
          Toast(this.$t('common.isblackScreen'));
        }
      },
      //保存场景
      saveScreen() {
        this.$router.push({ name: 'savesc' });
        this[this.chageName] = false;
        this.modelBox = false;
        let that = this;
        setTimeout(function() {
          that.tapEnd = true;
        }, 1000);
      },
      pressMove(eventname, e, hammer) {
        const $btn = this.$refs['btn'] || '';
        const styles = this.getStyle($btn) || '';
        let winHeight = window.innerHeight - 65;
        let winWidth = window.innerWidth - 60;
        let { width, height } = styles;
        let top = e.center.y - parseFloat(width) / 2;
        let left = e.center.x - parseFloat(height) / 2;
        if(left < 8) {
          left = 8;
        }
        if(left > winWidth) {
          left = winWidth - 8;
        }
        if(top < 55) {
          top = 55;
        }
        if(top > winHeight) {
          top = winHeight;
        }
        //拖动处理
        if(eventname === 'panmove') {
          if(!this.modelBox) {
            $btn.style.top = top + 'px';
            $btn.style.left = left + 'px';
            this.tapEnd = true;
          }

          e.preventDefault();
        }
        //点击处理
        if(eventname === 'tap') {
          if(this.tapEnd) {
            this.modelBox = true;
            this.tapEnd = false;
            if(left < winWidth / 3 && top > winHeight / 3 * 2) {
              console.log('左下角');
              this.changeLayoutLeftBottom = true;
              this.chageName = 'changeLayoutLeftBottom';
            }
            if(
              left < winWidth / 3 &&
              top > winHeight / 3 &&
              top < winHeight / 3 * 2
            ) {
              console.log('左中');
              this.changeLayoutLeftCenter = true;
              this.chageName = 'changeLayoutLeftCenter';
            }
            if(left < winWidth / 3 && top < winHeight / 3) {
              console.log('左上角');
              this.changeLayoutLeftTop = true;
              this.chageName = 'changeLayoutLeftTop';
            }
            if(left > winWidth / 3 * 2 && top < winHeight / 3) {
              console.log('右上角');
              this.changeLayoutRightTop = true;
              this.chageName = 'changeLayoutRightTop';
            }
            if(
              left > winWidth / 3 * 2 &&
              top < winHeight / 3 * 2 &&
              top > winHeight / 3
            ) {
              console.log('右中');
              this.changeLayoutRightCenter = true;
              this.chageName = 'changeLayoutRightCenter';
            }
            if(left > winWidth / 3 * 2 && top > winHeight / 3 * 2) {
              console.log('右下角');
              this.changeLayoutRightBottom = true;
              this.chageName = 'changeLayoutRightBottom';
            }
            if(
              left > winWidth / 3 &&
              left < winWidth / 3 * 2 &&
              top > winHeight / 3 &&
              top < winHeight / 3 * 2
            ) {
              console.log('正中');
              this.changeLayoutCenter = true;
              this.chageName = 'changeLayoutCenter';
            }
            if(
              left > winWidth / 3 &&
              left < winWidth / 3 * 2 &&
              top < winHeight / 3
            ) {
              console.log('上中');
              this.changeLayoutTopCenter = true;
              this.chageName = 'changeLayoutTopCenter';
            }
            if(
              left > winWidth / 3 &&
              left < winWidth / 3 * 2 &&
              top > winHeight / 3 * 2
            ) {
              console.log('下中');
              this.changeLayoutBottomCenter = true;
              this.chageName = 'changeLayoutBottomCenter';
            }
            this.getTestPicture();
            setTimeout(() => {
              this.timeOut = true;
            }, 150);
          }

        }
      },

      getStyle(element) {
        return window.getComputedStyle(element, null);
      }
    },
    computed: {}
  };
</script>
<style scoped lang="less">
  //当按钮在屏幕中间线的左下时，点击赋予的样式
  @background: rgba(110, 115, 129, 0.96);

  .changeLayoutLeftBottom {
    animation: clickLeftBottom 0.1s;
    animation-fill-mode: forwards;
  }

  @keyframes clickLeftBottom {
    from {
      width: 127px;
      height: 127px;
    }
    to {
      width: 508px;
      height: 508px;
      border-radius: 14px;
      background: @background;
      top: 406px;
      left: 67px;
      bottom: 6px;
      right: 53px;

      margin: auto;
    }
  }

  // 当按钮在屏幕中间线的左上时，点击赋予的样式
  .changeLayoutLeftTop {
    animation: clickLeftTop 0.1s;
    animation-fill-mode: forwards;
  }
  @keyframes clickLeftTop {
    from {
      width: 127px;
      height: 127px;
    }
    to {
      width: 508px;
      height: 508px;
      border-radius: 14px;
      background: @background;
    }
  }

  //当按钮在屏幕中间线的右上时，点击赋予的样式
  .changeLayoutRightTop {
    animation: clickRightTop 0.1s;
    animation-fill-mode: forwards;
  }
  @keyframes clickRightTop {
    from {
      width: 127px;
      height: 127px;
    }
    to {
      width: 508px;
      height: 508px;
      border-radius: 14px;
      background: @background;
      top: 206px;
      left: 200px;
      // bottom: 6.766px;
      // right: 53.766px;
      // margin: auto;
    }
  }
  //当按钮在屏幕中间线的右下时，点击赋予的样式
  .changeLayoutRightBottom {
    animation: clickRightBottom 0.1s;
    animation-fill-mode: forwards;
  }
  @keyframes clickRightBottom {
    from {
      width: 127px;
      height: 127px;
    }
    to {
      width: 508px;
      height: 508px;
      border-radius: 14px;
      background: @background;
      top: 676px;
      left: 167px;
      // right: 53.766px;
    }
  }
  // 左中样式
  .changeLayoutLeftCenter {
    animation: clickLeftCenter 0.1s;
    animation-fill-mode: forwards;
  }
  @keyframes clickLeftCenter {
    from {
      width: 127px;
      height: 127px;
    }
    to {
      width: 508px;
      height: 508px;
      border-radius: 14px;
      background: @background;
      top: 0;
      bottom: 0;
      left: 30px;
      margin: auto;
    }
  }
  //右中
  .changeLayoutRightCenter {
    animation: clickRightCenter 0.1s;
    animation-fill-mode: forwards;
  }
  @keyframes clickRightCenter {
    from {
      width: 127px;
      height: 127px;
    }
    to {
      width: 508px;
      height: 508px;
      border-radius: 14px;
      background: @background;
      top: 0;
      bottom: 0;
      left: 200px;
      margin: auto;
    }
  }
  //正中
  .changeLayoutCenter {
    animation: clickCenter 0.1s;
    animation-fill-mode: forwards;
  }
  @keyframes clickCenter {
    from {
      width: 127px;
      height: 127px;
    }
    to {
      width: 508px;
      height: 508px;
      border-radius: 14px;
      background: @background;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
    }
  }
  //上中
  .changeLayoutTopCenter {
    animation: clickTopCenter 0.1s;
    animation-fill-mode: forwards;
  }
  @keyframes clickTopCenter {
    from {
      width: 127px;
      height: 127px;
    }
    to {
      width: 508px;
      height: 508px;
      border-radius: 14px;
      background: @background;
      top: 100px;
      left: 0;
      right: 0;
      margin: 0 auto;
    }
  }
  //下中
  .changeLayoutBottomCenter {
    animation: clickBottomCenter 0.1s;
    animation-fill-mode: forwards;
  }
  @keyframes clickBottomCenter {
    from {
      width: 127px;
      height: 127px;
    }
    to {
      width: 508px;
      height: 508px;
      border-radius: 14px;
      background: @background;
      top: 690px;
      left: 0;
      right: 0;
      margin: 0 auto;
    }
  }
  .model-box {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: 88;
  }
  .tcnbtn-layout {
    background: #42bad9;
    position: fixed;
    border-radius: 50%;
    width: 127px;
    height: 127px;
    bottom: 250px;
    z-index: 1000;
    color: #ffffff;
  }
  .btnBox {
    width: 100%;
    height: 100%;
    padding: 45px 20px;
    display: flex;
    flex-wrap: wrap;
    // justify-content: space-between;
    .take,
    .cuts,
    .swap,
    .freeze,
    .black-screen,
    .save-screen {
      width: 33.33%;
      height: 40%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      font-size: 30px;
      border: 2px solid rgba(0, 0, 0, 0); /*no*/
      border-radius: 16px;
      img {
        width: 50%;
        height: auto;
        margin-bottom: 10px;
      }
    }
    .take {
      padding-top: 32px;
      img {
        width: 60%;
      }
    }
    .cuts {
      padding-top: 30px;
      img {
        width: 60%;
      }
    }
    .swap {
      padding-top: 30px;
      img {
        width: 60%;
      }
    }
    .freeze {
      padding-top: 20px;
      img {
        width: 60%;
      }
    }
    .black-screen {
      padding-top: 20px;
      img {
        width: 60%;
      }
    }
    .save-screen {
      padding-top: 20px;
      img {
        width: 60%;
      }
    }
  }
  .active {
    position: absolute;
    width: 90%;
    height: 90%;
    background: rgba(255, 255, 255, 0.2);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    > div {
      width: 15px;
      height: 15px;
      background: #fff;
      border-radius: 50%;
    }
    > div:nth-child(2) {
      margin: 15px 0;
    }
  }
  .border {
    border: 2px solid #ff6700 !important; /*no*/
    border-radius: 16px;
  }
  .color {
    color: #acb2c8;
  }
</style>


