<template>
  <div class="plusminus">
    <div class="minus btn" @touchstart="minusStart" @touchend="minusEnd"></div>
    <div class="inputbox"><input class="needsclick" type="text" :regex="regex" v-model="nums" @change="blur(nums,$event)" maxlength="6"></div>
    <div class="plus btn" @touchstart="plusStart" @touchend="plusEnd"></div>
  </div>
</template>

<script>
  import { Toast } from 'mint-ui';
  export default {
    props: {
      num: {
        default: 0
      },
      max: {
        default: 100
      },
      min: {
        default: 0
      },
      regex: {
        default: '^[1-9]*[1-9][0-9]*$'
      },
      label: {
        default: ''
      }
    },
    data: function() {
      return {
        nums: 0,
        interval: null
      };
    },
    created() {
      this.nums = Number.parseInt(this.num);
    },
    methods: {
      validatePrint(str) {
        var reg = new RegExp(this.regex);
        if(!reg.test(str)) {
          return false;
        }
        return true;
      },
      blur(nums, $event) {
        let flag = true;
        if(this.validatePrint($event.target.value)) {
          let targetValue = parseInt($event.target.value);
          let _value = Math.max(Math.min(targetValue, this.max), this.min);
          if(_value != targetValue) {
            Toast({
              className: 'vx-toast',
              message: this.label + this.min + ' ' + this.$t('settings.enterResolutionErrMsgs.flag') + ' ' + this.max,
              duration: 1500
            });
            flag = false;
            this.nums = _value;
          }
        } else {
          Toast({
            className: 'vx-toast',
            message: this.label + this.min + ' ' + this.$t('settings.enterResolutionErrMsgs.flag') + ' ' + this.max,
            duration: 1500
          });
          flag = false;
          this.nums = this.min < 0 ? 0 : this.min;
        }
        this.$emit('update', this.nums, flag);
        let that = this;
        setTimeout(function() {
          that.$emit('touchend', flag);
        }, 100);
      },
      plusHandler() {
        this.nums = Number.parseInt(this.nums);
        if(this.nums < this.max) {
          this.nums = this.nums + 1;
        }
        if(this.nums === this.max) {
          this.nums = this.max;
        }
      },
      minusHandler() {
        this.nums = Number.parseInt(this.nums);
        if(this.nums > this.min) {
          this.nums = this.nums - 1;
        }
      },
      emitHandler() {
        setTimeout(() => {
          this.$emit('touchend');
        }, 0);
      },
      plusStart() {
        this.interval = setInterval(() => {
          this.plusHandler();
        }, 100);
      },
      plusEnd() {
        clearInterval(this.interval);
        this.plusHandler();
        this.emitHandler();
      },
      minusStart() {
        this.interval = setInterval(() => {
          this.minusHandler();
        }, 100);
      },
      minusEnd() {
        clearInterval(this.interval);
        this.minusHandler();
        this.emitHandler();
      },
      watchHandler(newval, oldval) {
        if(this.validatePrint(newval)) {
          this.$emit('update', newval);
        } else {
          if(newval != '') {
            this.nums = oldval;
          }
        }
      }
    },
    watch: {
      num: function(val) {
        this.nums = Number.parseInt(val);
      },
      nums: function(newval, oldval) {
        if(newval != '') {
          if(newval[0] === '-' && newval[0].length > 1) {
            this.watchHandler(newval, oldval);
          } else if(newval[0] === '-' && newval[0].length == 1) {
            return false;
          } else {
            this.watchHandler(newval, oldval);
          }
        }
      }
    }
  };
</script>


<style lang="less" scoped>
  .plusminus {
    width: 340px;
    height: 60px;
    position: absolute;
    right: 0;
    top: 10px;
    display: flex;
    border: 1px solid #aaaaaa; /*no*/
    border-radius: 2px; /*no*/
    .minus,
    .plus {
      height: 100%;
      width: 90px;
    }
    .minus {
      background: ~"url('../assets/common/g-minus.png') no-repeat" center;
      background-size: 30px 30px;
    }
    .plus {
      background: ~"url('../assets/common/g-add.png') no-repeat" center;
      background-size: 30px 30px;
    }
    .inputbox {
      flex: 1;
      height: 100%;
      border-left: 1px solid #aaaaaa; /*no*/
      border-right: 1px solid #aaaaaa; /*no*/
      input {
        width: 100%;
        height: 100%;
        border: 0;
        background-color: #383940;
        color: #dddddd;
        text-align: center;
        font-size: 16px; /*no*/
      }
    }
  }
</style>




