<template>
  <div class="uploader">
    <input capture="camera" v-if="isAndroid" type="file" :accept="accept" :multiple="multiple" :id="id" @change="changeFile" style="display:none;">
    <input v-else type="file" :accept="accept" :multiple="multiple" :id="id" @change="changeFile" style="display:none;">
  </div>
</template>

<script>
  import { mapMutations } from 'vuex';
  import { Toast } from 'mint-ui';
  import { browser } from '../utils';

  export default {
    name: 'imguploader',
    data() {
      return {
        id: `uploader${Math.random().toString(36).substr(3, 3)}`, // 唯一id
        uploadFileLength: 0, //已经上传文件的数量
        getopidanduploadurl: false //上传前获得异步上传地址
      };
    },
    props: {
      multiple: {
        type: Boolean,
        default: false
      },
      accept: {
        type: String,
        default: 'image/jpeg,image/png'   // example: 'image/jpeg,image/png'
      },
      singleFileSize: {     // singleFile maxsize oversize
        type: Number,
        default: 5242880000000
      },
      fileMaxLength: {     // allfile size that is smart to computed
        type: Number,
        default: 10
      }
    },
    computed: {
      isAndroid() {
        return browser().isAndroid;
      }
    },
    methods: {
      ...mapMutations(['setUploadImg']),
      createEventBroadcast() {
        let ev = new window.MouseEvent('click', {
          canceable: false,
          bubble: true
        });
        document.getElementById(this.id).dispatchEvent(ev);
      },
      changeFile(e) {
        let fileslist = e.target.files;
        //通过检测 允许上传
        if(fileslist && this.checkFiles(fileslist)) {
          var nowFile = fileslist[0];
          //js reader方法强制生成base64的字符串
          let reader = new FileReader();
          reader.readAsDataURL(nowFile);
          reader.onload = event => {
            this.setUploadImg({ type: 0, src: nowFile });
            this.$emit('go');
          };
        }
      },
      checkFiles(fileslist) {
        let allowUp = true;
        if(this.uploadFileLength + fileslist.length > this.fileMaxLength) {
          Toast('抱歉，您上传的数量超过我们的限制咯');
          allowUp = false;
          return allowUp;
        }
        for(var i in fileslist) {
          if(fileslist.hasOwnProperty(i)) {
            let nowFile = fileslist[i];
            if(this.accept.split(',').indexOf(nowFile.type) < 0) {
              Toast('抱歉，您上传的文件我们不支持哦');
              allowUp = false;
              return allowUp;
            }
            if(nowFile.size > this.singleFileSize) {
              Toast('抱歉，您上传的文件体积超过我们的限制');
              allowUp = false;
              return allowUp;
            }
          }
        }
        return allowUp;
      }
    }
  };
</script>





