import Vue from 'vue';
import Vuex from 'vuex';
import { serialize, getLoc, setLoc, getSen, setSen } from '../utils';

Vue.use(Vuex);

export default new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production', //在非生产环境下，使用严格模式
  state: {
    cacheData: getSen('cacheData') || {},
    lang: getLoc('lang') || 'zh',
    isHScreen: window.orientation === 90 || window.orientation === -90,
    selectedRate: getSen('selectedRate') || {},
    customRate: getSen('customRate') || {},
    isVcan6S: getSen('isVcan6S') || false,
    hasNewVersion: getSen('hasNewVersion') || 0,
    portLists: getLoc('port') || [],
    uploadImg: {
      type: 0, //0新增，1编辑
      name: '', //图片名称
      src: '', //图片路径（文件）
      id: '' //图片id（新增时无id）
    },
    screenStructure: getSen('screenStructure') || {},
    progressTime: {} || getSen('progressTime')
  },
  getters: {
    getCacheData: state => serialize(state.cacheData),
    getLang: state => state.lang,
    getIsHScreen: state => state.isHScreen,
    getSelectedRate: state => serialize(state.selectedRate),
    getIsVcan6S: state => state.isVcan6S,
    getNewVersion: state => state.hasNewVersion,
    getCustomRate: state => serialize(state.customRate),
    getPortList: state => serialize(state.portLists),
    getUploadImg: state => state.uploadImg,
    getScreenStructure: state => serialize(state.screenStructure),
    getProgressTime: state => state.progressTime
  },
  mutations: {
    setCacheData(state, data) {
      state.cacheData = { ...state.cacheData, ...data };
      setSen('cacheData', state.cacheData);
    },
    setLang(state, data) {
      state.lang = data;
      setLoc('lang', data);
    },
    setIsHScreen(state, data) {
      state.isHScreen = data;
      setSen('isHScreen', state.isHScreen);
    },
    setSelectedRate(state, data) {
      state.selectedRate = data;
      setSen('selectedRate', state.selectedRate);
    },
    setIsVcan6S(state, data) {
      state.isVcan6S = data;
      setSen('isVcan6S', state.isVcan6S);
    },
    setNewVersion(state, data) {
      state.hasNewVersion = data;
      setSen('hasNewVersion', state.hasNewVersion);
    },
    setCustomRate(state, data) {
      state.customRate = data;
      setSen('customRate', state.customRate);
    },
    setUploadImg(state, data) {
      state.uploadImg = { ...state.uploadImg, ...data };
    },
    setSelectPort(state, data) {
      data.forEach((v, i) => {
        state.portLists.push(v);
      });
    },
    clearSelectPort(state) {
      state.portLists = [];
    },
    setScreenStructure(state, data) {
      state.screenStructure = data;
      setSen('screenStructure', data);
    },
    setProgressTime(state, data) {
      state.progressTime = data;
      setSen('progressTime', data);
    }
  },
  actions: {}
});



