<template>
  <div class="vx-input-warp">
    <div class="title">{{placeholder}}</div>
    <input class="vx-input" :type="type" v-model="currentValue" @input="change" :placeholder="placeholdertext" @blur="blur">
  </div>
</template>

<script>
  export default {
    data() {
      return {
        currentValue: '',
        regex: '^[1-9]*[1-9][0-9]*$'
      };
    },
    props: ['type', 'placeholder', 'value', 'placeholdertext'],
    created() {
      this.currentValue = this.value;
    },
    watch: {
      value(val) {
        this.currentValue = Number.isNaN(Number.parseInt(val)) ? '' : Number.parseInt(val);
      },
      currentValue(newval, oldval) {
        if(newval != '') {
          this.currentValue = this.watchHandler(newval, oldval);
        }
        this.$emit('input', this.currentValue);
      }
    },
    methods: {
      change(evt) {
        this.currentValue = evt.target.value;
      },
      blur(evt) {
        this.$emit('changedInput', this.currentValue);
      },
      validatePrint(str) {
        var reg = new RegExp(this.regex);
        if(!reg.test(str)) {
          return false;
        }
        return true;
      },
      watchHandler(newval, oldval) {
        if(this.validatePrint(newval)) {
          return newval;
        } else {
          if(newval != '') {
            return oldval;
          } else {
            return newval;
          }
        }
      }
    }
  };
</script>

<style lang="less" scoped>
  .vx-input-warp {
    display: flex;
    border: 1px solid #787e8d; /*no*/
    margin: 15px 20px;
    border-radius: 5px;
    background-color: #5e636e;
    .title {
      width: 140px;
      text-align: left;
      height: 96px;
      line-height: 96px;
      text-indent: 20px;
      color: #ffffff;
      font-size: 30px;
    }
    .vx-input {
      flex: 1;
      width: 100%;
      height: 96px;
      border: 0px;
      outline: none;
      background-color: #5e636e;
      color: #aaacaf;
      text-indent: 20px;
      font-size: 28px;
    }
  }
</style>




