import axios from 'axios';
import { Indicator, Toast } from 'mint-ui';
import { getLoc } from '../utils';
import api from './config';
let Loading = false;
let timer = null;
const notLoadApi = ['setWinZOrder', 'setWinParam', 'setWinInput', 'setWinStatus'];

function checkUrl(url) {
  return notLoadApi.some(uri => {
    return url.indexOf(uri) > 0;
  });
}
////添加请求拦截器
axios.interceptors.request.use(
  config => {
    if (!checkUrl(config.url)) {
      Loading = true;
    }
    return config;
  },
  error => {
    Loading = false;
    return Promise.reject(error);
  }
);

//添加响应拦截
axios.interceptors.response.use(
  response => {
    Loading = false;
    Indicator.close();
    return response;
  },
  error => {
    Loading = false;
    Indicator.close();
    return Promise.resolve(error.response);
  }
);

function checkStatus(response) {
  if (response && response.status === 200) {
    return response.data;
  }
  let locale = getLoc('lang') == null ? 'zh' : getLoc('lang');
  return {
    status: 501,
    msg: locale == 'zh' ? '网络异常' : 'Network exception'
  };
}

function checkCode(res) {
  if (res.status === 501) {
    Toast(res.msg);
  }

  if (res.status === 10000001) {
    let locale = getLoc('lang') == null ? 'zh' : getLoc('lang');
    if (res.errorCode[0] === 20010101) {
      Toast(locale == 'zh' ? '服务器异常' : 'Server exception');
    }
  }
  return res;
}

export default {
  post(url, data, { loading = true } = {}) {
    if (loading) {
      clearTimeout(timer);
      timer = setTimeout(() => {
        if (Loading) {
          Indicator.open({
            text: getLoc('lang') == 'en' ? 'Loading...' : '加载中...',
            spinnerType: 'triple-bounce'
          });
        }
      }, 2000);
    }

    if (url.type) {
      return this.get(url, data);
    } else {
      return axios({
        method: 'post',
        baseURL: process.env.NODE_ENV === 'development' ? '' : window.apihost,
        url: url.url,
        data: JSON.stringify(data),
        timeout: 10000,
        headers: {
          'X-Requested-With': 'XMLHttpRequest',
          'Content-Type': 'application/json; charset=UTF-8'
        }
      })
        .then(response => {
          return checkStatus(response);
        })
        .then(res => {
          return checkCode(res);
        });
    }
  },
  get(url, params, { loading = true } = {}) {
    if (loading) {
      clearTimeout(timer);
      timer = setTimeout(() => {
        if (Loading) {
          Indicator.open({
            text: getLoc('lang') == 'en' ? 'Loading...' : '加载中...',
            spinnerType: 'triple-bounce'
          });
        }
      }, 2000);
    }

    if (url.type) {
      return new Promise((resolve, reject) => {
        return resolve(api[url.url]);
      });
    } else {
      return axios({
        method: 'get',
        baseURL: process.env.NODE_ENV === 'development' ? '' : window.apihost,
        url: url.url,
        params, // get 请求时带的参数
        timeout: 10000,
        headers: {
          'X-Requested-With': 'XMLHttpRequest'
        }
      })
        .then(response => {
          return checkStatus(response);
        })
        .then(res => {
          return checkCode(res);
        });
    }
  },
  all() {
    return axios.all.call(this, arguments);
  },
  upload(url, data, processCallback) {
    if (url.type) {
      return new Promise((resolve, reject) => {
        return resolve(api[url.url]);
      });
    } else {
      return axios({
        method: 'post',
        baseURL: process.env.NODE_ENV === 'development' ? '' : window.apihost,
        url: url.url,
        data: data,
        headers: {
          'X-Requested-With': 'XMLHttpRequest',
          'Content-Type': 'multipart/form-data'
        },
        onUploadProgress(progressEvent) {
          var complete = ((progressEvent.loaded / progressEvent.total) * 100) | 0;
          processCallback && processCallback(complete);
        },
        timeout: 60 * 60 * 1000
      })
        .then(response => {
          return checkStatus(response);
        })
        .then(res => {
          return checkCode(res);
        });
    }
  }
};



