<template>
  <div>
    <div class="message-box">
      <div class="title">{{title}}</div>
      <div class="message">
        <input type="text" v-model.trim="message" :maxlength="maxlength||null">
      </div>
      <div class='commit'>
        <div @click='cancel' class="btn">{{$t('scenarist.cancel')}}</div>
        <div @click='sure' class="btn">{{$t('scenarist.ok')}}</div>
      </div>
    </div>
    <div class="mask-model"></div>
  </div>
</template>

<script>
  export default {
    props: ['title', 'maxlength', 'msg'],
    data() {
      return {
        message: this.msg ? this.msg : null
      };
    },
    methods: {
      cancel() {
        this.$emit('cancel');
      },
      sure() {
        this.$emit('sure', this.message);
      }
    }
  };
</script>
<style lang="less" scoped>
  .message-box {
    width: 531px;
    height: 292px;
    background: rgba(106, 110, 124, 0.96);
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    border-radius: 16px;
    padding-top: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    color: #ffffff;
  }
  .title {
    font-size: 34px;
  }
  .message {
    width: 541px;
    height: 64px;
    border-radius: 16px;
    display: flex;
    justify-content: center;
  }
  .message > input {
    width: 80%;
    height: 100%;
    display: block;
    outline: none;
    padding: 0 10px;
    border: 1px solid #8890a2; /*no*/
    border-radius: 8px;
    background: #5e636e;
    color: #ffffff;
    font-size: 28px;
  }
  .commit {
    width: 100%;
    height: 94px;
    border-radius: 0 0 16px 16px;
    background-attachment: #eef0f1;
    display: flex;
    justify-content: space-between;
    border-top: 1px solid #8890a2;
    font-size: 30px;
    div {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    div:nth-child(1) {
      flex: 1;
      height: 100%;
      background: #565a65;
      border-radius: 0 0 0 16px;
      border-right: 1px solid #8890a2;
      color: #c2c2c2;
    }
    div:nth-child(2) {
      flex: 1;
      height: 100%;
      background: #565a65;
      border-radius: 0 0 16px 0;
      border-left: 1px solid #8890a2;
      color: #ffffff;
    }
  }
  .mask-model {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    z-index: 9;
  }
</style>


